body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-Light-webfont.f0fc91f2.woff) format("woff");
  font-style: normal;
  font-weight: 300;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-LightItalic-webfont.ac5bc917.woff) format("woff");
  font-style: italic;
  font-weight: 300;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-Regular-webfont.b3be96cb.woff) format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-Italic-webfont.6440668c.woff) format("woff");
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-Medium-webfont.d3e528d9.woff) format("woff");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-MediumItalic-webfont.054c6f1a.woff) format("woff");
  font-style: italic;
  font-weight: 500;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-Bold-webfont.388938f6.woff) format("woff");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Roboto";
  src: url(/civic-link/static/media/Roboto-Black-webfont.d9526533.woff) format("woff");
  font-style: normal;
  font-weight: 900;
}

* {
  margin: 0;
  padding: 0px;
  box-sizing: border-box;
}

.flex-large-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-top: 5px;
  margin-left: 25px;
}

.flex-card-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-top: 15px;
}

.flex-card {
  padding: 18px;
}

header {
  font-weight: 800;
  font-size: 52px;
  line-height: 1em;
  color: #FDCF2C;
  margin-bottom: 25px;
}

header:hover {
  color: #ffc400; /* I didn't know which color to pick, so please help me fix this later */
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 36px;
  font-style: italic;
  color: #2ba6eb;
  margin-bottom: 5px;
}

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 24px;
  color: #727272;
}

h2.get-involved:hover {
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 24px;
  color: #2ba6eb;
}

h3.home {
  color: black;
}

h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 24px;
  color: #727272;
}

h5 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 20px;
  text-transform: none;
  color: #000000;
}

h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-transform: lowercase;
  color: #000000;
}

p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #000000;
}

.mission {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #000000;
}

.App {
  text-align: left;
  padding-top: 100px;
}

.App-body {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: left;
          justify-content: left;
}

div.home {
  float: left;
  width: 55%;
  padding-left: 25px;
}

img.home {
  width: 100%;
}

html {
  background-color: #f9f9f9;
}

.space {
  line-height: 3em;
}

.break {
  line-height: 2em;
}

.aside {
  line-height: 3em
}

hr {
  height: 2px;
  border: 0px;
  background-color: #2ba6eb;
  margin: 30px;
}

/* Container holding the image and the text */
.container {
  position: relative;
}

/* Bottom right text */
.text-block {
  position: absolute;
  top: 100px;
  left: 100px;
  width: 300px;
  background-color: #f9f9f9;
  padding: 30px;
}

