@font-face {
  font-family: "Roboto";
  src: url("Roboto-Light-webfont.woff") format("woff");
  font-style: normal;
  font-weight: 300;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-LightItalic-webfont.woff") format("woff");
  font-style: italic;
  font-weight: 300;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Regular-webfont.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Italic-webfont.woff") format("woff");
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Medium-webfont.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-MediumItalic-webfont.woff") format("woff");
  font-style: italic;
  font-weight: 500;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Bold-webfont.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Black-webfont.woff") format("woff");
  font-style: normal;
  font-weight: 900;
}

* {
  margin: 0;
  padding: 0px;
  box-sizing: border-box;
}

.flex-large-container {
  display: flex;
  justify-content: flex-start;
  margin-top: 5px;
  margin-left: 25px;
}

.flex-card-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-top: 15px;
}

.flex-card {
  padding: 18px;
}

header {
  font-weight: 800;
  font-size: 52px;
  line-height: 1em;
  color: #FDCF2C;
  margin-bottom: 25px;
}

header:hover {
  color: #ffc400; /* I didn't know which color to pick, so please help me fix this later */
  transition: 0.3s;
}

h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 36px;
  font-style: italic;
  color: #2ba6eb;
  margin-bottom: 5px;
}

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 24px;
  color: #727272;
}

h2.get-involved:hover {
  color: black;
  transition: 0.3s;
}

h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 24px;
  color: #2ba6eb;
}

h3.home {
  color: black;
}

h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 24px;
  color: #727272;
}

h5 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 20px;
  text-transform: none;
  color: #000000;
}

h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-transform: lowercase;
  color: #000000;
}

p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #000000;
}

.mission {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #000000;
}

.App {
  text-align: left;
  padding-top: 100px;
}

.App-body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
}

div.home {
  float: left;
  width: 55%;
  padding-left: 25px;
}

img.home {
  width: 100%;
}

html {
  background-color: #f9f9f9;
}

.space {
  line-height: 3em;
}

.break {
  line-height: 2em;
}

.aside {
  line-height: 3em
}

hr {
  height: 2px;
  border: 0px;
  background-color: #2ba6eb;
  margin: 30px;
}

/* Container holding the image and the text */
.container {
  position: relative;
}

/* Bottom right text */
.text-block {
  position: absolute;
  top: 100px;
  left: 100px;
  width: 300px;
  background-color: #f9f9f9;
  padding: 30px;
}
